import React, { useEffect, useState } from 'react';
import {
  Typography, Paper, Box, TextField, InputAdornment, Button,
  Table, TableBody, TableCell, TableContainer, TableHead, TableRow,
  Dialog, DialogActions, DialogContent, DialogTitle, 
  FormControl, InputLabel, Select, MenuItem, Chip, Grid,
  Tabs, Tab, Divider
} from '@mui/material';
import {
  Search as SearchIcon,
  Visibility as VisibilityIcon,
  CheckCircle as CheckCircleIcon,
  Cancel as CancelIcon,
  LocalShipping as ShippingIcon
} from '@mui/icons-material';
import axios from 'axios';
import '../css/detailOrder.css';

const Order = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [status, setStatus] = useState('');
  const [openDialog, setOpenDialog] = useState(false);
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [tabValue, setTabValue] = useState(0);
  const [orders, setOrders] = useState([]);

  const getData = async () => {
    const reponse = await axios.get('/order');
    const result = reponse.data;
    console.log(result.data);
    setOrders(result.data);
  }

  useEffect(() => {
    getData();
  }, []);

  const handleOpenDialog = (order) => {
    setSelectedOrder(order);
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
    setSelectedOrder(null);
  };

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };

  const getStatusColor = (status) => {
    switch(status) {
      case ' hon thnh': return { color: 'success', text: ' hon thnh' };
      case 'ang nu': return { color: 'info', text: 'ang nu' };
      case 'Ch x l': return { color: 'warning', text: 'Ch x l' };
      case ' hy': return { color: 'error', text: ' hy' };
      default: return { color: 'default', text: status };
    }
  };

  const filteredOrders = orders.filter(order => {
  
    const statusMatch = status === '' ? true : order.status === status;

    const searchTermMatch = searchTerm === '' ? true : (
      order.id.toString().includes(searchTerm) ||
      order.customer?.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      order.customer?.phone?.includes(searchTerm)
    );
    
    return statusMatch && searchTermMatch;
  });

  const processOrderDetails = (detailOrders) => {
    // To mt object  nhm cc chi tit theo productId
    const groupedItems = {};
    
    // Nhm cc chi tit theo productId
    detailOrders.forEach(item => {
      const key = item.productId;
      
      if (!groupedItems[key]) {
        // Nu sn phm cha tn ti trong nhm, to mi
        groupedItems[key] = {
          productId: item.productId,
          productName: item.product.name,
          quantity: item.quantityProduct,
          price: item.priceProduct,
          baseTotal: item.priceProduct * item.quantityProduct,
          toppings: []
        };
      }
      
      // Nu khng phi dummy topping (toppingId = 0)
      if (item.toppingId !== 0) {
        // Thm topping vo sn phm
        groupedItems[key].toppings.push({
          id: item.toppingId,
          name: item.topping.name,
          price: item.priceTopping
        });
      }
    });
    
    // Chuyn i object thnh mng  render
    return Object.values(groupedItems);
  };

  const handleUpdate = async (order) => {
    const response = await axios.put(`/order`, order, {
      headers: {
        'Content-Type': 'application/json',
      },
    });
    console.log(response.data);
    getData();
    
  }

  const handleCancel = async (order) => {
   const response = await axios.put(`/order/cancel`, order, {
      headers: {
        'Content-Type': 'application/json',
      },
    });
    console.log(response.data);
    getData();
  }

  return (
    <>
      <Typography variant="h4" gutterBottom fontWeight="bold" color="#333">
        Qun l n hng
      </Typography>

      <Paper sx={{ p: 3, mb: 3, borderRadius: '12px' }}>
        <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 3 }}>
          <Tabs 
            value={tabValue} 
            onChange={handleTabChange} 
            indicatorColor="primary"
            textColor="inherit"
            sx={{
              '& .MuiTab-root.Mui-selected': {
                color: '#ff5a5f',
              },
            }}
          >
            <Tab label="Tt c n hng" onClick={() => setStatus('')} />
            <Tab label="Ch x l" onClick={() => setStatus('Ch x l')} />
            <Tab label="ang nu" onClick={() => setStatus('ang nu')} />
            <Tab label=" hon thnh" onClick={() => setStatus(' hon thnh')} />
            <Tab label=" hy" onClick={() => setStatus(' hy')} />
          </Tabs>
        </Box>
        
        <Box display="flex" justifyContent="space-between" alignItems="center" mb={3}>
          <TextField
            placeholder="Tm kim n hng..."
            variant="outlined"
            size="small"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            sx={{ width: '300px' }}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <SearchIcon />
                </InputAdornment>
              ),
            }}
          />
        </Box>
        
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>M n hng</TableCell>
                <TableCell>Khch hng</TableCell>
                <TableCell>Ngy t</TableCell>
                <TableCell>Trng thi</TableCell>
                <TableCell>Tng tin</TableCell>
                <TableCell align="right">Hnh ng</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {filteredOrders.map((order) => (
                <TableRow key={order.id}>
                  <TableCell>{order.id}</TableCell>
                  <TableCell>{order.customer.name}</TableCell>
                  <TableCell>{order.created_at}</TableCell>
                  <TableCell>
                    <Chip 
                      label={getStatusColor(order.status).text}
                      color={getStatusColor(order.status).color}
                      size="small"
                    />
                  </TableCell>
                  <TableCell>{order.totalPrice}</TableCell>
                  <TableCell align="right">
                    <Button 
                      size="small" 
                      variant="outlined" 
                      sx={{ mr: 1 }}
                      onClick={() => handleOpenDialog(order)}
                      startIcon={<VisibilityIcon />}
                    >
                      Chi tit
                    </Button>
                    
                    {order.status === 'Ch x l' && (
                      <Button 
                        size="small" 
                        variant="contained" 
                        color="primary"
                        startIcon={<ShippingIcon />}
                        sx={{ mr: 1 }}
                        onClick = {() => handleUpdate(order)}
                      >
                        Chp nhn
                      </Button>
                    )}
                    
                    {(order.status === 'Ch x l' || order.status === 'ang nu') && (
                      <Button 
                        size="small" 
                        variant="contained" 
                        color="error"
                        startIcon={<CancelIcon />}
                        onClick ={() => handleCancel(order)}
                      >
                        Hy
                      </Button>
                    )}
                    
                    {order.status === 'ang nu' && (
                      <Button 
                        size="small" 
                        variant="contained" 
                        color="success"
                        startIcon={<CheckCircleIcon />}
                        sx={{ mr: 1 }}
                        onClick = {() => handleUpdate(order)}
                      >
                        Hon tt
                      </Button>
                    )}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
      
      {/* Order Detail Dialog */}
      <Dialog 
        open={openDialog} 
        onClose={handleCloseDialog} 
        maxWidth="md" 
        fullWidth
        className="order-detail-dialog"
        sx={{
          '& .MuiDialog-paper': {
            display: 'flex',
            flexDirection: 'column',
            maxHeight: '85vh',
          }
        }}
      >
        <DialogTitle>
          Chi tit n hng {selectedOrder?.id}
        </DialogTitle>
        <DialogContent sx={{ 
          overflow: 'auto', 
          display: 'flex',
          flexDirection: 'column',
          paddingBottom: 0,
          flex: 1
        }}>
          {selectedOrder && (
            <Grid container spacing={2}>
              <Grid item xs={6}>
                <Box className="customer-info-section">
                  <Typography variant="subtitle1" className="info-title">Thng tin khch hng</Typography>
                  <Box sx={{ mt: 2 }}>
                    <Typography variant="body1" className="info-row">
                      <span className="info-label">Tn:</span> {selectedOrder.customer.name}
                    </Typography>
                    <Typography variant="body1" className="info-row">
                      <span className="info-label">in thoi:</span> {selectedOrder.customer.phone}
                    </Typography>
                  </Box>
                </Box>
              </Grid>
              
              <Grid item xs={6}>
                <Box className="order-info-section">
                  <Typography variant="subtitle1" className="info-title">Thng tin n hng</Typography>
                  <Box sx={{ mt: 2 }}>
                    <Typography variant="body1" className="info-row">
                      <span className="info-label">Trng thi:</span> 
                      <Chip 
                        label={getStatusColor(selectedOrder.status).text}
                        color={getStatusColor(selectedOrder.status).color}
                        size="small"
                        sx={{ ml: 1 }}
                      />
                    </Typography>
                    <Typography variant="body1" className="info-row">
                      <span className="info-label">Ngy t:</span> {selectedOrder.created_at}
                    </Typography>
                    <Typography variant="body1" className="info-row">
                      <span className="info-label">Ngy cp nht:</span> {selectedOrder.updated_at}
                    </Typography>
                    <Typography variant="body1" className="info-row">
                      <span className="info-label">Tng tin:</span> {selectedOrder.totalPrice}
                    </Typography>
                  </Box>
                </Box>
              </Grid>
              
              <Grid item xs={12} className="product-details-section">
                <Divider sx={{ my: 2 }} />
                <Typography variant="subtitle1" className="product-details-title">Chi tit sn phm</Typography>
                <TableContainer sx={{ mt: 2 }} className="product-table">
                  <Table size="small">
                    <TableHead className="table-header">
                      <TableRow>
                        <TableCell className="table-header-cell">Sn phm</TableCell>
                        <TableCell align="center" className="table-header-cell">S lng</TableCell>
                        <TableCell align="right" className="table-header-cell">Gi</TableCell>
                        <TableCell align="right" className="table-header-cell">Thnh tin</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {processOrderDetails(selectedOrder.detailOrders).map((item, index) => {
                        // Tnh tng gi topping
                        const toppingTotal = item.toppings.reduce((sum, topping) => sum + topping.price, 0);
                        // Tnh tng tin cho mi sn phm (bao gm topping)
                        const itemTotal = item.price * item.quantity + toppingTotal * item.quantity;
                        
                        return (
                          <TableRow key={`${item.productId}-${index}`}>
                            <TableCell className="product-cell">
                              <div className="product-name">{item.productName}</div>
                              {item.toppings.length > 0 && (
                                <div className="topping-list">
                                  {item.toppings.map((topping, idx) => (
                                    <div key={`${topping.id}-${idx}`} className="topping-item">
                                      {topping.name}: {topping.price}
                                    </div>
                                  ))}
                                </div>
                              )}
                            </TableCell>
                            <TableCell align="center" className="quantity-cell">{item.quantity}</TableCell>
                            <TableCell align="right" className="price-cell">{item.price}</TableCell>
                            <TableCell align="right" className="total-cell">{itemTotal}</TableCell>
                          </TableRow>
                        );
                      })}
                      <TableRow className="order-total-row">
                        <TableCell colSpan={3} align="right" className="order-total-cell">Tng cng</TableCell>
                        <TableCell align="right" className="order-total-amount">{selectedOrder.totalPrice}</TableCell>
                      </TableRow>
                    </TableBody>
                  </Table>
                </TableContainer>
              </Grid>
            </Grid>
          )}
        </DialogContent>
        <DialogActions className="dialog-actions">
          <Button onClick={handleCloseDialog} color="inherit" className="action-button">ng</Button>
          
          {selectedOrder?.status === 'pending' && (
            <>
              <Button 
                variant="contained" 
                color="primary"
                startIcon={<ShippingIcon />}
                className="action-button"
              >
                Giao hng
              </Button>
              <Button 
                variant="contained" 
                color="error"
                startIcon={<CancelIcon />}
                className="action-button"
              >
                Hy n
              </Button>
            </>
          )}
          
          {selectedOrder?.status === 'shipping' && (
            <Button 
              variant="contained" 
              color="success"
              startIcon={<CheckCircleIcon />}
              className="action-button"
            >
              Hon tt n hng
            </Button>
          )}
        </DialogActions>
      </Dialog>
    </>
  );
};

export default Order;